<template>
  <div class="list row">
    <div class="col-md-8">
      <div class="input-group mb-3">
        <input type="text" class="form-control" placeholder="Search by title"
          v-model="title"/>
        <div class="input-group-append">
          <button class="btn btn-outline-secondary" type="button"
            @click="searchTitle"
          >
            Search
          </button>
        </div>
      </div>
    </div>
    <div class="col-md-6">
      <h4>Lists </h4>
      <ul class="list-group">
        <li class="list-group-item"
          :class="{ active: index == currentIndex }"
          v-for="(list, index) in lists"
          :key="index"
          @click="setActiveList(list, index)"
        >
          {{ list.title }}
        </li>
      </ul>

      <button class="m-3 btn btn-sm btn-danger" @click="removeAllLists">
        Remove All
      </button>
    </div>
    <div class="col-md-6">
      <div v-if="currentList">
        <h4>List</h4>
        <div>
          <label><strong>Title:</strong></label> {{ currentList.title }}
        </div>
        <div>
          <label><strong>Description:</strong></label> {{ currentList.description }}
        </div>
        <div>
          <label><strong>Status:</strong></label> {{ currentList.published ? "Published" : "Pending" }}
        </div>

        <router-link :to="'/list/' + currentList.id" class="badge badge-warning">Edit</router-link>
      </div>
      <div v-else>
        <br />
        <p>Please click on a List...</p>
      </div>
    </div>
  </div>
</template>

<script>
import ListDataService from "../services/ListDataService";

export default {
  name: "lists-list",
  data() {
    return {
      lists: [],
      currentList: null,
      currentIndex: -1,
      title: ""
    };
  },
  methods: {
    retrieveLists() {
      ListDataService.getAll()
        .then(response => {
          this.lists = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveLists();
      this.currentList = null;
      this.currentIndex = -1;
    },

    setActiveList(list, index) {
      this.currentList = list;
      this.currentIndex = list ? index : -1;
    },

    removeAllLists() {
      ListDataService.deleteAll()
        .then(response => {
          console.log(response.data);
          this.refreshList();
        })
        .catch(e => {
          console.log(e);
        });
    },
    
    searchTitle() {
      ListDataService.findByTitle(this.title)
        .then(response => {
          this.lists = response.data;
          this.setActiveList(null);
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    }
  },
  mounted() {
    this.retrieveLists();
  }
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>
